import React from "react";
import { Link } from 'react-router-dom';
import me from "../../assets/img/profile_headshot.jpeg"
import pd from "../../assets/resume/Kriti_React_Native_Developer_1Year.pdf"
import Services from "../Services";
import Skills from "../Skills";
import { FaHandPointDown } from "react-icons/fa";

const AboutInfo = (props) => {
  return (
    <div>
      <section id="about" className="about">
        <div className="container" data-aos="fade-up">
          <div className="section-title">
            <h2>About</h2>
            <p>Let me introduce myself</p>
          </div>
          <div className="row">
            <div className="col-lg-4">
              <img src={me} className="img-fluid" alt="" />
            </div>
            <div className="col-lg-8 pt-4 pt-lg-0 content">
              <h3>Hi, I'm Kriti Arora.</h3>
              <h2>A Software Professional based in India.</h2>
              <p>
                I love taking on new challenges. Software development is something that I like from heart as problem solving and logic building is something that I am really good at. 
                I’ve always sought out opportunities and challenges that are meaningful to me.
                I truely love learning new things and taking on new challenges.
                Currently, I'm a working with Whitehat Jr., India.
              </p>
              <p>
                I’m an avid traveller and a music lover.
              </p>
              <p>
                Wanna know more? A more detailed look at my professional capabilities and experience is available below.
              </p>
              <p style={{ color: "#eebd73", marginBottom: "100px" }}>
                Scroll Down!
                <FaHandPointDown />
              </p>
              <a target="_blank" rel="noreferrer" href={pd} className="button uppercase">Download Resume</a>
              <Link href className="button uppercase" to='/contact'>Contact Me</Link>
            </div>
          </div>
        </div>
      </section>
      <Skills />
      <Services />
    </div>
  )
}

export default AboutInfo;