import React from "react";
import arrowImage from "../../assets/down-arrow.png"

const Hero = (props) => {
  return (
    <section id="hero" className="d-flex flex-column justify-content-center">
      <div className="container" data-aos="zoom-in" data-aos-delay={100}>
        <h1>Kriti Arora</h1>
        <p>Software Professional | Fullstack Developer<span className="typed" data-typed-items="Software Engineer, Fullstack Developer, Freelancer" /></p>
        <div className="social-links">
          <a target="_blank" rel="noreferrer" href="https://www.instagram.com/_updating_soon_/" className="instagram"><i className="bx bxl-instagram" /></a>
          <a target="_blank" rel="noreferrer" href="https://www.linkedin.com/in/kriti-arora-b9666818b/" className="linkedin"><i className="bx bxl-linkedin" /></a>
          <a target="_blank" rel="noreferrer" href="https://github.com/kritiarora143" className="github"><i className="bx bxl-github" /></a>
        </div>
      </div>
      <div className="hero-scrollDown">
        <p >Scroll </p>
        <img src={arrowImage} alt="ScrollDown Arrow" />
      </div>
    </section>
  )
}

export default Hero;